import React from 'react';

import { useAppDispatch, useAppSelector } from '../../../../redux/hooks';

import {
  setTimer,
  showTimer,
  showHideTimer,
} from '../../../../redux/timers/timersSlice';
import Toggler from '../../../views/toggler/toggler.component';
import initialTimerSettings from '../../../timer/initial-timer-settings';

interface IProps {
  appName: string,
};

const TimerControl: React.FC<IProps> = (props: IProps): JSX.Element => {
  // const TimerControl: React.FC<IProps> = (props: IProps): JSX.Element => {

  const dispatch = useAppDispatch();
  const initialShowTimer = false;
  const stateTimer = useAppSelector(showTimer)?.timers;
  const stateShowTimer = stateTimer?.[props.appName]?.show || initialShowTimer;

	/**
	 * React hook useEffect for initiate timer state for the 
	 */
  React.useEffect(() => {
    dispatch(setTimer(initialTimerSettings(props.appName)));
  }, [initialShowTimer]);

  const handleChange = () => {
    console.log(
      "%c :::::::::::::::: 37 line of handleChange() of TimerControl.tsx ::::::::::::::::",
      "color: maroon; font-weight: bold",
      "\n",
      "stateShowTimer ===> ",
      stateShowTimer,
      "\n",
    );
    dispatch(showHideTimer({timer: props.appName, show: !stateShowTimer}));
  };

  const content = {
    togglerAddSubtract: {
      toggleBox: {
        className: ""
      },
      label: {
        htmlFor: "timer-toggler",
        span: {
          left: {
            className: "toggler description__paragraph--level-three",
            text: "hide timer"
          },
          right: {
            className: "toggler description__paragraph--level-two",
            text: "show timer"
          }
        }
      },
      input: {
        type: "checkbox",
        className: "toggler__input",
        id: "timer-toggler"
      }
    }
  };

  console.log(
    "%c :::::::::::::::: 75 line of TimerControl.tsx ::::::::::::::::",
    "color: olive; font-weight: bold",
    "\n",
    "stateShowTimer ===> ",
    stateShowTimer,
    "\n",
    "stateTimer ===> ",
    stateTimer,
    "\n",
    "props ===> ",
    props,
    "\n",
    "useAppSelector(showTimer) ===> ",
    useAppSelector(showTimer),
    "\n",
  );

  return (
    <Toggler
      {...content.togglerAddSubtract}
      checked={stateShowTimer}
      onChange={handleChange}
    />
  );
};

export default TimerControl;
