import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { RootState } from '../store';

import initialMultiplicationTabSettings from '../../pages/multiplication-tab/initial-multiplication-tab-settings';

import { getStorage } from '../../utils/process-local-storage/process-local-storage';

import ITimer from '../../TS/interfaces/ITimer';

import initialTimerSettings from '../../components/timer/initial-timer-settings'
import { Applications } from '../../TS/types/enumApplications';

export interface TimerState {
  timers?: ITimer,
}

const initialState: TimerState = {
  // timers: initialTimerSettings(''),
};

const localStorageData = (state: TimerState, settings: TimerState[]) => {
  return {
    timer: state.timers,
  }
}

export const timersSlice = createSlice({
  name: "timers",
  // `createSlice` will infer the state type from the `initialState` argument
  initialState,
  reducers: {
    // 1) set initial value
    setTimer: (state, action: PayloadAction<Record<string, any>>) => {
      state.timers = Object.assign(
        {},
        {...action.payload}
      );
      console.log(
        "%c :::::::::::::::: 54 line of setTimer() of timersSlice.ts ::::::::::::::::",
        "color: navy; font-weight: bold",
        "\n",
        "action.payload ===> ",
        action.payload,
        "\n",
        "state.timers ===> ",
        state.timers,
        "\n",
        "Object.assign(...) ===> ",
        Object.assign(
          {},
          action.payload
        ),
        "\n",
      );
    },
    // 2) show timer
    showHideTimer: (state, action: PayloadAction<{ timer: string; show: boolean; }>) => {
      const newTimerState = state.timers || {};
      const { timer, show } = action.payload;

      const newSettings = Object.assign(
        {},
        newTimerState[action.payload.timer],
        { show: show }
      );
      
      newTimerState[timer] = newSettings;

      state.timers = newTimerState;

      console.log(
        "%c :::::::::::::::: 75 line of showHideTimer() of timersSlice.ts ::::::::::::::::",
        "color: crymson; font-weight: bold",
        "\n",
        "action.payload ===> ",
        action.payload,
        "\n",
        "state.timers ===> ",
        state.timers,
        "\n",
        "Object.assign(...) ===> ",
        Object.assign(
          {},
          state.timers,
          { showTimer: action.payload },
        ),
        "\n",
      );
    },
  },
});

export const {
  // setInputValue,
  // clearAllInputs,
  // switchSubtract,
  setTimer,
  showHideTimer,
} = timersSlice.actions;

export const showTimer = (state: RootState) => state.timers;

export default timersSlice;