import ITimer from '../../TS/interfaces/ITimer';
import { Applications } from '../../TS/types/enumApplications';



const initialTimerSettings = (application: Applications | string): ITimer => {

  return {
    [application]: {
      show: false,
      pause: false,
      days: '00',
      houurs: '00',
      minutes: '00',
      seconds: '00',
    }
  };
}

export default initialTimerSettings;